<template>
  <div class="shelves-content">
    <div class="empty" v-if="!showTable">
      <h3>请在左侧选择一个仓库库位</h3>
    </div>
    <div class="content" v-else>
        <el-row :span="24">
          <el-col>
            <common-table>
              <template slot="table">
                <el-table
                  ref="shelvesTable"
                  stripe
                  :data="leafNode"
                  @selection-change="selected => selectRow = selected">
                  <!--<el-table-column type="selection" width="30" ></el-table-column>-->
                  <el-table-column type="index" label="序号" width="50"></el-table-column>
                  <el-table-column sortable prop="name" label="库位名称"></el-table-column>
                  <el-table-column prop="type" label="库位类型"></el-table-column>
                  <el-table-column sortable prop="number" label="库位编号"></el-table-column>
                  <el-table-column sortable prop="size" label="容量"></el-table-column>
                  <el-table-column sortable prop="length" label="长度"></el-table-column>
                  <el-table-column sortable prop="width" label="宽度"></el-table-column>
                  <el-table-column prop="wareHouseName" label="所属仓库"></el-table-column>
                  <el-table-column prop="desc" label="描述"></el-table-column>
                </el-table>
              </template>
            </common-table>
          </el-col>
        </el-row>
    </div>
  </div>
</template>

<script>
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import NavButton from '@/components/navButton'

// import { getShelvesDetail } from '@/api/organization'

export default {
  name: 'ShelvesDetail',
  data () {
    return {
      showTable: false,
      selectRow: [],
      tableData: [],
      leafNode:[]
    }
  },
  props: {
    nodeData: {
      type: Object,
      required: true
    },
    isInit: {
      type: Boolean
    }
  },
  watch: {
    nodeData: {
      handler (nodeData) {
        this.getDetail()
      },
      deep: true
    }
  },
  methods: {
    refresh (type) {
      this.getDetail()
    },
    initDetail () {
      this.orgNodeData = null
    },
    getDetail () {
      if (this.nodeData) {
        this.leafNode = []
        this.getLeafNode(this.nodeData)
        this.showTable = true
        // })
      }
    },
    getLeafNode (nodeData) {
      if(nodeData['children'].length){
        for(let child of nodeData['children']){
          this.getLeafNode(child)
        }
      }else{
        this.leafNode.push(nodeData)
      }

    },
    isButtonIsDisabled () {
      return !!this.selectRow.length
    },
    // 菜单 -- 新增
    handleAdd () {
      this.$emit('update:isCreate', true)
      this.$emit('openDialog', {})
    },
    // 菜单 -- 编辑
    edit () {
      this.$emit('update:isCreate', false)
      this.$emit('openDialog', [this.selectRow])
    },
    cancel () {
      this.$confirm('确认删除选中的库位吗?', '提示', {
        type: 'warning'
      }).then(() => {
        let params = {
          shelvesId: []
        }
        for(let item of this.selectRow){
          params.shelvesId.push(item.id)
        }
        this.$emit('deleteShelvesList', params)
      }).catch(() => {})
    }
  },
  components: {
    CommonWrapper,
    CommonTable,
    NavButton
  }
}
</script>

<style lang="scss" scoped>
  .shelves-content {
    display: flex;
    flex-direction: column;
    align-items: center;

  .empty{
    padding:30px;
    font-size:2rem;
    color: darken($base-light-color, 15%);
  }
  .content {
      width:99%;
      max-width:1200px;
  }
  }
</style>



